/**
 * 
 */
const toast = function(title) {
	uni.showToast({
		icon: 'none',
		title: title
	})
}

/**
 * 
 */
const checkMobile = function(value) {
	let reg = /^1\d{10}$/
	if (value == '') {
		toast('');
		return false;
	} else if (!reg.test(value)) {
		toast('');
		return false;
	}
	return true;
}

/**
 * 
 */
const checkNickname = function(value) {
	if (value == '') {
		toast('');
		return false;
	} else if (value.length < 2) {
		toast('');
		return false;
	} else if (value.length > 32) {
		toast('');
		return false;
	}
	return true;
}

/**
 * 
 */
const checkPassword = function(value) {
	if (value == '') {
		toast('');
		return false;
	} else if (value.length < 6) {
		toast('6');
		return false;
	}
	return true;
}

/**
 * 
 */
const checkNewPassword = function(value) {
	if (value == '') {
		toast('');
		return false;
	} else if (value.length < 6) {
		toast('6');
		return false;
	}
	return true;
}

/**
 * 
 */
const checkVerifyCode = function(value) {
	let reg = /^\d{4}$/
	if (value == '') {
		toast('');
		return false;
	} else if (!reg.test(value)) {
		toast('');
		return false;
	}
	return true;
}

/**
 * 
 */
const checkIdCard = function(value) {
	let reg = /^\d{15}|\d{18}$/
	if (value == '') {
		toast('');
		return false;
	} else if (!reg.test(value)) {
		toast('1518');
		return false;
	}
	return true;
}

/**
 * 
 */
const checkBankCard = function(value) {
	let reg = /^([1-9]{1})(\d{14}|\d{15}|\d{16}|\d{18})$/
	if (value == '') {
		toast('');
		return false;
	} else if (!reg.test(value)) {
		toast('');
		return false;
	}
	return true;
}

export default {
	checkMobile,
	checkNickname,
	checkPassword,
	checkNewPassword,
	checkVerifyCode,
	checkIdCard,
	checkBankCard
};
